
*{margin:0;padding: 0; box-sizing: border-box;}
html,body{height: 100vh;background: radial-gradient(#ff2d2d,#700202 );}

.brd{border:1px solid #ccc;}

.center{display: flex; justify-content: center; align-items: center;}
.meio{display: flex; justify-content: space-around; align-items: center;}
.space{display: flex;justify-content: space-between; align-items: center;}

section{height: 600px;}
.topo{height: 60px; font-size: 9pt;color: #fff;border-bottom:2px solid rgb(198, 60, 60); }
.topo input{width: 400px;color: #fff;font-size: 16pt; background: transparent;border: 0; border-bottom:2px solid #830101 ;outline: none;  }

nav{height: 80px;padding: 2em;}
.menu{width: 650px;}
::placeholder{color: rgb(133, 0, 0);}
.menu ul li{list-style: none; display: inline-block; padding: 2em; font-weight: bold; }
.link{text-decoration: none; color: #fff;}
.link:hover{color: #ddc401;}
.quemSomos{height: 600px; background-color: #fff;}
.slide{height: 350px; padding: 2em;}
.info{width: 350px;height: 100%; padding: 1em; color: #fff;}
.slide img{animation: logo 2s ease ;}
.info h1{font-size: 32pt;}
.yellow{position: relative;left: 3em; color:#890101c7 ; background-color: #f6a525; height: 350px; width: 450px;}
.cor{color: #f6a525;}
.menus{margin: 0 auto; max-width: 950px; height: 350px; text-align: center; color: #f6a525;text-align: center;flex-wrap: wrap;}
.itens{animation-delay: 1s; animation: acao 2s; width: 350px; height: 150px;}
.itemProd p{color: #fff; }



@media screen and (max-width: 768px){
    section{width: 100%;}
    .topo{text-align: center; width: 100%; display: block; padding: 1em; height: auto;}
    .topo input{width: 250px;}
    .info{width: 100%;}
    nav{width: 100%; display: block; font-size: 9pt;}
    .logo{width: 100%;}
    .logo img{width: 100%;}
    .menu{width: 100%; height: auto; display: block;}
    .meio {width: 100%; height: auto; display: block;}
    .menu ul li{width: 100%; display: inline;}
    .slide{display: block;height: 450px;}
    .slide img{width: 100%; }
    .yellow{width: 100%; display: block;position: none;left: 0; height: 180px;}
    .quemSomos{width: 100%; display: block;}
    .quemSomos img{width: 100%;}
    .menus{width: 100%; display: block;padding: 1em; }
    .itens{ width: 100%; display: block; margin-top: 1em; }
    
    
}

@keyframes acao{
    from{
        opacity: 0;
        transform: translatey(150px);
        
    }
}
@keyframes logo {
    0%{
        transform: scale(0);
    }
   50%{
   transform: scale(1.2);
   }
   100%{
    transform: scale(1);
   }
    
}